
import {AssetManager} from '../asset-manager'
import {Level} from '../level'
import { LevelSpec } from '../level/level';

export class LevelPlayer {
  private level : Level;

  constructor(
    public spec:LevelSpec,
    private am:AssetManager,
    private parent:HTMLElement)
  {
    this.level = new Level(spec,this.am); 
    this.parent.appendChild(this.level.app.view);
    this.parent.appendChild(this.level.hud.div);
  }

  load() {
  }

  remove() {
  }

  isPlaying() {
    return this.level.isPlaying();
  }

  pause() {
    this.level.pause();
  }

  resume() {
    this.level.resume();
  }
}


