
export function svgToCanvas(svgmarkup:string,background?:string) {
  return new Promise((resolve:(canvas:HTMLCanvasElement)=>void) => {
    let div = document.createElement('div');
    div.innerHTML = svgmarkup;
    let svg = div.querySelector('svg');

    let width = parseInt(svg!.getAttribute('width')!);
    let height = parseInt(svg!.getAttribute('height')!);

    let canvas = document.createElement('canvas');
    canvas.width = width;
    canvas.height = height;
    let ctx = canvas.getContext('2d')!;
    let svgdataurl = 'data:image/svg+xml;utf8,'+svgmarkup.replace(/#/g,'%23');
    let img = new Image();

    img.onload = () => {
      if(background) {
        ctx.fillStyle = background;
        ctx.fillRect(0,0,width,height);
      }
      ctx.drawImage(img,0,0);
      resolve(canvas);
    };
    img.src = svgdataurl;
  });
}

/**
 * Calls callback at specified time intervals
 */
export class Timer {
  private count:number;
  private tlast:number;

  constructor(
    public interval:number, private callback:(count:number)=>void)
  {
    this.count = 0;
    this.tlast = NaN;
  }

  tick(t:number) {
    if(isNaN(this.tlast)) {
      this.tlast = t;
    } else {
      let elapsed = t-this.tlast;
      if(elapsed >= this.interval) {
        this.callback(this.count++);
        this.tlast = t;
      }
    }
  }
}

export const EPSILON = 1e-4;

export const vec2 = {
  add: function (...varr:number[][]) {
    let answer = [0, 0];
    for (let v of varr) {
      answer[0] += v[0];
      answer[1] += v[1];
    }
    return answer;
  },

  sub: function (va:number[], vb:number[]) {
    return [va[0] - vb[0], va[1] - vb[1]];
  },

  mul: function (va:number[], k:number) {
    return [va[0] * k, va[1] * k];
  },

  isNonZero: function (v:number[], tolerance = EPSILON) {
    return Math.abs(v[0]) > tolerance || Math.abs(v[1]) > tolerance;
  },

  isZero: function (v:number[], tolerance = EPSILON) {
    return !vec2.isNonZero(v, tolerance);
  },

  lenSq: function (v:number[]) {
    return v[0] * v[0] + v[1] * v[1];
  },

  len: function (v:number[]) {
    return Math.sqrt(vec2.lenSq(v));
  },

  unit: function (v:number[]) {
    let len = vec2.len(v);
    if (len !== 0) {
      return vec2.mul(v, 1 / vec2.len(v));
    } else {
      return [0, 0];
    }
  },

  distSq: function (va:number[], vb:number[]) {
    return vec2.lenSq(vec2.sub(va, vb));
  },

  dist: function (va:number[], vb:number[]) {
    return Math.sqrt(vec2.distSq(va, vb));
  },

  dot: function (va:number[], vb:number[]) {
    return va[0] * vb[0] + va[1] * vb[1];
  },

  /**
   * Z-coordinate of cross product (because x and y are zero)
   */
  cross: function (va:number[], vb:number[]) {
    return va[0] * vb[1] - va[1] * vb[0];
  },

  toInt: function (v:number[]) {
    return [Math.round(v[0]), Math.round(v[1])];
  },

  equal: function (va:number[], vb:number[], tolerance = EPSILON) {
    return Math.abs(va[0] - vb[0]) < tolerance &&
      Math.abs(va[1] - vb[1]) < tolerance;
  },

  low: function (...points:number[][]) {
    let xlow = Infinity, ylow = Infinity;
    for (let point of points) {
      xlow = Math.min(point[0], xlow);
      ylow = Math.min(point[1], ylow);
    }
    return [xlow, ylow];
  },

  high: function (...points:number[][]) {
    let xhigh = -Infinity, yhigh = -Infinity;
    for (let point of points) {
      xhigh = Math.max(point[0], xhigh);
      yhigh = Math.max(point[1], yhigh);
    }
    return [xhigh, yhigh];
  },

  format: function (v:number[]) {
    return '[' + v[0].toFixed(2) + ',' + v[1].toFixed(2) + ']';
  },

  dir: function (vfrom:number[], vto:number[]) {
    return vec2.unit(vec2.sub(vto, vfrom));
  },

  orthogonal: function (point:number[]) {
    let [x,y] = point;
    return [y, -x];
  }

};

