
import {Terra,TerraSpec} from './terra'

export class HexTerra extends Terra {

  constructor(spec:TerraSpec) {
    super(spec);
  }

  clone() {
    return new HexTerra({
      nrows : this.nrows,
      ncols : this.ncols,
      cellsize : this.cellsize,
      weights : this.weightGrid.data,
      resolvable : false
    });
  }

  get radiusInner() {
    return this.cellsize * Math.sin(Math.PI/3);
  }

  get radiusOuter() {
    return this.cellsize;
  }

  deriveGridSize() {
    let s = this.cellsize;
    let h = this.radiusInner;
    let W = this.width;
    let H = this.height;
    this.ncols = Math.floor((W/s)*(2/3) + 1);
    this.nrows = Math.ceil((H/(2*h))+1);
  }

  deriveTerraDimensions() {
    let s = this.cellsize;
    let h = this.radiusInner;
    this.width = s*3*(this.ncols-1)/2;
    this.height = 2*(this.nrows-1)*h;
  }

  public getCellCenter(row:number, col:number) {
    let s = this.cellsize;
    let h = this.radiusInner;
    let cx,cy;
    if(col%2) { // Odd
      cx = (0.5+col+Math.floor(col/2)) * s;
      cy = (2*row+1) * h;
    } else { // Even
      cx = (3*col/2) * s;
      cy = 2*row*h;
    }
    return [cx,cy];
  }

  protected getCellCorners(row:number, col:number) {
    let s = this.cellsize;
    let h = this.radiusInner;
    let [cx,cy] = this.getCellCenter(row,col);
    return [
      [cx+s,cy],
      [cx+s/2,cy+h],
      [cx-s/2,cy+h],
      [cx-s,cy],
      [cx-s/2,cy-h],
      [cx+s/2,cy-h]
    ];
  }

  protected distanceManhattan(a:number[],b:number[]) : number {
    return Math.abs(a[0]-b[0]) + Math.abs(a[1]-b[1]);
  }

  protected isValidCellCoord(coord:number[]) {
    let [row,col] = coord;
    // Row min check
    if(row < 0) {
      return false;
    }

    // Row max check
    if(col%2) {
      if(row >= this.nrows-1) {
        return false;
      }
    } else {
      if(row >= this.nrows) {
        return false;
      }
    }

    // Column min check
    if(col < 0) {
      return false;
    }
    // Column max check
    if(col >= this.ncols) {
      return false;
    }
    return true;
  }

  public getNeighbors(row:number,col:number) : number[][] {
    let neighbors = [
      [row+1,col],
      [row-1,col]
    ];
    if(col%2) {
      neighbors.push([row,col-1]);
      neighbors.push([row+1,col-1]);
      neighbors.push([row,col+1]);
      neighbors.push([row+1,col+1]);
    } else {
      neighbors.push([row-1,col-1]);
      neighbors.push([row,col-1]);
      neighbors.push([row-1,col+1]);
      neighbors.push([row,col+1]);
    }
    return neighbors.filter(coord => {
      return this.isValidCellCoord(coord);
    });
  }

  protected heuristic(from:number[], to:number[]) : number {
    return this.distanceManhattan(from,to);
  }
}


