
import * as createKDTree from 'static-kdtree'

export class Grid<T> {

  data : T[];

  constructor(public nrows:number, public ncols:number, data?:T[]) {
    if(data) {
      console.assert(data.length === nrows*ncols);
      this.data = data.slice();
    } else {
      this.data = new Array(this.nrows*this.ncols);
    }
  }

  get(row:number, col:number) {
    return this.data[row*this.ncols+col];
  }

  set(row:number, col:number, value:T) {
    this.data[row*this.ncols+col] = value;
  }

  fill(value:T) {
    this.data.fill(value);
  }

  forEach(callback:(row:number,col:number,value:T)=>void) {
    for(let row=0; row<this.nrows; row++) {
      for(let col=0; col<this.ncols; col++) {
        callback(row,col,this.get(row,col));
      }
    }
  }
}

export interface TerraSpec {
  width? : number;
  height? : number;

  nrows? : number;
  ncols? : number;

  weights? : number[];
  cellsize : number;
  resolvable : boolean;
}


export abstract class Terra {

  protected nrows : number;
  protected ncols : number;
  protected _cellsize : number;
  protected width : number;
  protected height : number;

  public weightGrid : Grid<number>;

  private gCostGrid : Grid<number>;
  private hCostGrid : Grid<number>;
  private camefromGrid : Grid<number[]|null>;
  private bfsData : any;

  protected kdt : any;
  protected cellCoords : number[][];

  private areWeightsDirty : boolean;

  constructor(spec:TerraSpec) {
    this._cellsize = spec.cellsize;
    this.areWeightsDirty = true;

    if(spec.nrows && spec.ncols) {
      this.nrows = spec.nrows;
      this.ncols = spec.ncols;
      this.deriveTerraDimensions();
    } else if(spec.width && spec.height) {
      this.width = spec.width;
      this.height = spec.height;
      this.deriveGridSize();
    } else {
      throw new Error("Incomplete TerraGridSpec");
    }

    this.weightGrid = new Grid<number>(this.nrows,this.ncols,spec.weights);
    if(!spec.weights) {
      this.weightGrid.fill(1);
    }

    if(spec.resolvable) {
      this.indexForCellResolution();
    }

    this.bfsData = {};
  }

  private indexForCellResolution() {
    let cellCenters:number[][] = [];
    this.cellCoords = [];
    this.forEachCell((row:number,col:number) => {
      cellCenters.push(this.getCellCenter(row,col));
      this.cellCoords.push([row,col]);
    });
    this.kdt = createKDTree(cellCenters);
  }

  get cellsize() {
    return this._cellsize;
  }

  get numrows() {
    return this.nrows;
  }

  get numcols() {
    return this.ncols;
  }

  /**
   * Compute [[nrows]] and [[ncols]] assuming that [[width]] and [[height]]
   * are set
   */
  abstract deriveGridSize() : void;

  /**
   * Compute [[width]] and [[height]] assuming that [[nrows]] and [[ncols]]
   * are set
   */
  abstract deriveTerraDimensions() : void;

  forEachCell(callback:(row:number,col:number)=>void) {
    for(let row=0; row<this.nrows; row++) {
      for(let col=0; col<this.ncols; col++) {
        if(this.isValidCellCoord([row,col])) {
          callback(row,col);
        }
      }
    }
  }

  getCellDebugInfo(row:number,col:number) {
    interface Info {
      weight : number;
      bfsData : any;
    }
    let info:Info = {
      weight:this.weightGrid.get(row,col),
      bfsData:{}
    };
    for(let key in this.bfsData) {
      info.bfsData[key] =
        'from '+this.bfsData[key].camefromGrid.get(row,col)+
        ' d '+this.bfsData[key].distanceGrid.get(row,col)
    }
    return JSON.stringify(info);
  }

  cornersForEachCell(callback:(corners:number[][],row:number,col:number)=>void) {
    for(let row=0; row<this.nrows; row++) {
      for(let col=0; col<this.ncols; col++) {
        if(this.isValidCellCoord([row,col])) {
          callback(this.getCellCorners(row,col),row,col);
        }
      }
    }
  }

  setWeight(row:number,col:number,weight:number) {
    this.weightGrid.set(row,col,weight);
  }

  getWeight(row:number,col:number) {
    return this.weightGrid.get(row,col);
  }

  resolveCell(x:number, y:number) {
    let cellCenterIndex = this.kdt.nn([x,y]);
    if(cellCenterIndex >= 0) {
      return this.cellCoords[cellCenterIndex];
    } else {
      console.assert(false);
      return null;
    }
  }

  protected abstract isValidCellCoord(coord:number[]) : boolean;
  protected abstract getCellCorners(i:number,j:number) : number[][];
  public abstract getCellCenter(i:number, j:number) : number[];

  protected abstract distanceManhattan(a:number[],b:number[]) : number;
  public abstract getNeighbors(row:number,col:number) : number[][];
  protected abstract heuristic(from:number[], to:number[]) : number;

  private allocateGrids(algo='a*',key?:number) {
    if(algo === 'a*' || algo === 'dijkstra') {
      if(!this.gCostGrid) {
        this.gCostGrid = new Grid<number>(this.nrows,this.ncols);
      }
      if(!this.hCostGrid) {
        this.hCostGrid = new Grid<number>(this.nrows,this.ncols);
      }
      if(!this.camefromGrid) {
        this.camefromGrid = new Grid<number[]|null>(this.nrows,this.ncols);
      }
    } else if(algo === 'bfs') {
      console.assert(key !== undefined);
      if(!this.bfsData[key!]) {
        this.bfsData[key!] = {
          camefromGrid : new Grid<number[]|null|number>(this.nrows,this.ncols),
          distanceGrid : new Grid<number>(this.nrows,this.ncols)
        };
      }
    } else {
      console.assert(false);
    }
  }

  private hasBFSDataForTarget(targetCell:number[]) {
    let [row,col] = targetCell;
    let key = this.generateCellKey(row,col);
    return this.bfsData[key] !== undefined;
  }

  private doBFSAnalysis(targetCell:number[]) {
    let [row,col] = targetCell;
    let key = this.generateCellKey(row,col);
    this.allocateGrids('bfs',key);

    let frontier:number[][] = [];
    let {camefromGrid,distanceGrid} = this.bfsData[key];
    camefromGrid.fill(0);
    distanceGrid.fill(0);
    camefromGrid.set(row,col,null);
    frontier.push(targetCell);

    while(frontier.length) {
      let current = frontier.shift()!;
      for(let next of this.getNeighbors(current[0],current[1])) {
        if(camefromGrid.get(next[0],next[1]) === 0) {
          if(this.weightGrid.get(next[0],next[1]) !== 0) {
            frontier.push(next);
            camefromGrid.set(next[0],next[1],current);
            distanceGrid.set(next[0],next[1],
              distanceGrid.get(current[0],current[1])+1);
          }
        }
      }
    }
    this.areWeightsDirty = false;
  }

  private reconstructPath(goal:number[],camefromGrid:Grid<number[]|null>) {
    let path = [goal];
    let node = goal;
    while(camefromGrid.get(node[0],node[1])) {
      node = camefromGrid.get(node[0],node[1])!;
      path.unshift(node);
    }
    return path;
  }

  private searchOneToOnePath(start:number[],goal:number[],algo='a*') : number[][]
  {
    let openSet:number[][] = [];
    let closedSet:number[][] = [];
    if(algo === 'a*') {
      openSet = [start];
    } else if(algo === 'dijkstra') {
      for(let i=0; i<this.nrows; i++) {
        for(let j=0; j<this.ncols; j++) {
          openSet.push([i,j]);
        }
      }
    }

    let lowestFScoreNode = () => {
      let idx = -1;
      let lowest = Infinity;
      for(let i=0; i<openSet.length; i++) {
        let [row,col] = openSet[i];
        let f = this.gCostGrid.get(row,col) + this.hCostGrid.get(row,col);
        if(f < lowest) {
          lowest = f;
          idx = i;
        }
      }
      if(idx < 0) {
        // All costs are infinity, pick first one arbitrarily
        idx = 0;
      }
      return openSet.splice(idx,1)[0];
    }

    let inOpenSet = (coord:number[]) => {
      let [row,col] = coord;
      for(let i=0; i<openSet.length; i++) {
        if(openSet[i][0] === row && openSet[i][1] === col) {
          return true;
        }
      }
      return false;
    }

    let inClosedSet = (coord:number[]) => {
      let [row,col] = coord;
      for(let i=0; i<closedSet.length; i++) {
        if(closedSet[i][0] === row && closedSet[i][1] === col) {
          return true;
        }
      }
      return false;
    }

    this.gCostGrid.set(start[0], start[1], 0);
    this.hCostGrid.set(start[0], start[1], this.heuristic(start, goal));

    while(openSet.length > 0) {
      let cur = lowestFScoreNode();
      if(cur[0] === goal[0] && cur[1] === goal[1]) {
        return this.reconstructPath(goal,this.camefromGrid);
      }

      closedSet.push(cur);

      let neighbors = this.getNeighbors(cur[0],cur[1]);
      for(let neighbor of neighbors) {

        let [row,col] = neighbor;

        if(this.isWallAt(row,col)) {
          continue;
        }

        // If neighbor is in closedSet, it's already evaluated. Ignore it
        if(inClosedSet(neighbor)) {
          continue;
        }

        // If neighbor is not in openSet, add it to it
        if(!inOpenSet(neighbor)) {
          openSet.push(neighbor);
        }

        // Distance from start to the neighbor
        let g = this.gCostGrid.get(cur[0],cur[1]) +
          this.distanceManhattan(cur,neighbor);

        if(g > this.gCostGrid.get(neighbor[0],neighbor[1])) {
          continue; // this is not a better path
        }

        this.camefromGrid.set(neighbor[0],neighbor[1], cur);
        this.gCostGrid.set(neighbor[0],neighbor[1], g);

        if(algo === 'dijkstra') {
          this.hCostGrid.set(neighbor[0],neighbor[1],0);
        } else if(algo === 'a*') {
          this.hCostGrid.set(neighbor[0],neighbor[1],
            this.heuristic(neighbor,goal));
        } else {
          console.assert(false);
        }
      }
    }
    return [];
  }

  public isWallAt(row:number, col:number) {
    return this.weightGrid.get(row,col) === 0;
  }

  public setWallAt(row:number, col:number) {
    this.weightGrid.set(row,col,0);
    this.areWeightsDirty = true;
  }

  public removeWallAt(row:number, col:number) {
    this.weightGrid.set(row,col,1);
    this.areWeightsDirty = true;
  }

  public generateCellKey(row:number,col:number) {
    return row*this.ncols+col;
  }

  private searchAllToOnePath(start:number[],goal:number[]) : number[][] {
    if(!this.hasBFSDataForTarget(goal) || this.areWeightsDirty) {
      this.doBFSAnalysis(goal);
    }
    let key = this.generateCellKey(goal[0],goal[1]);
    let path = this.reconstructPath(start, this.bfsData[key].camefromGrid);
    return path.reverse();
  }

  searchPath(start:number[], goal:number[], algo='bfs') : number[][] {

    if(algo === 'dijkstra') {

      this.allocateGrids(algo);
      this.gCostGrid.fill(Infinity);
      this.hCostGrid.fill(0);
      this.camefromGrid.fill(null);
      return this.searchOneToOnePath(start,goal,algo);

    } else if(algo === 'a*') {

      this.allocateGrids(algo);
      this.gCostGrid.fill(Infinity);
      this.hCostGrid.fill(0);
      this.camefromGrid.fill(null);
      return this.searchOneToOnePath(start,goal,algo);

    } else if(algo === 'bfs') {

      return this.searchAllToOnePath(start,goal);

    } else {
      throw new Error('Unknown algo:'+algo);
    }
  }

  toSVG(fillopacity=1.0) {
    /*
              /^\
             / | \
         S  /  |  \  S
           /   H   \
          /    |    \
         / 60  |  60 \
        /______v______\
        <------S------>
        
    */
    let shapes = '';
    for(let row=0; row<this.nrows; row++) {
      for(let col=0; col<this.ncols; col++) {
        let corners = this.getCellCorners(row,col);
        let points = corners.map(c => `${c[0]},${c[1]}`).join(' ');
        let fill;
        fill = this.weightGrid.get(row,col) ? '#ffffff' : '#000000';
        shapes += `<polygon points="${points}" stroke="#aaaaaa"`+
          ` fill="${fill}" fill-opacity="${fillopacity}" class="cell"`+
          ` id="cell${row}-${col}"></polygon>`;
      }
    }

    return `<svg
      xmlns:dc="http://purl.org/dc/elements/1.1/"
      xmlns:cc="http://creativecommons.org/ns#"
      xmlns:rdf="http://www.w3.org/1999/02/22-rdf-syntax-ns#"
      xmlns:svg="http://www.w3.org/2000/svg"
      xmlns="http://www.w3.org/2000/svg"
      xmlns:sodipodi="http://sodipodi.sourceforge.net/DTD/sodipodi-0.dtd"
      xmlns:inkscape="http://www.inkscape.org/namespaces/inkscape"
      version="1.1"
      width="${this.width}" height="${this.height}">
      <g id="weights" data="${this.nrows},${this.ncols}" `+
      `inkscape:label="Weights" inkscape:groupmode="layer">
      ${shapes}
      </g>
      <g id="terrain" inkscape:label="Terrain Art" inkscape:groupmode="layer">
      </g>
      <g id="structures" inkscape:label="Structures Art"
       inkscape:groupmode="layer">
      </g>
      <g id="actors" inkscape:label="Actors Art" inkscape:groupmode="layer">
      </g>
    </svg>`;
  }
}


