
class CubicBezier {

  public degree : number;

  constructor(public cpoints:number[][]) {
    this.degree = cpoints.length - 1;
  }

  evaluate(t:number) {
    let p = this.cpoints;
    let t2 = t*t;
    let t3 = t*t2;
    let one_t2 = (1-t) * (1-t);
    let one_t3 = (1-t) * one_t2;
    let x = one_t3*p[0][0] + 3*t*one_t2*p[1][0] + 3*t2*(1-t)*p[2][0] + t3*p[3][0];
    let y = one_t3*p[0][1] + 3*t*one_t2*p[1][1] + 3*t2*(1-t)*p[2][1] + t3*p[3][1];
    return [x, y];
  }
}

/**

 Tweening
 --------

        ^                   cp2           cp3
    1.0 |                   O             O-------+
        |                                 .
        |                                 .
        |                                 .
        |                                 .
        |                                 .
        |                                 .
        |                                 .
        |    cp0                          .
    0.0 +-----O             O             .
        |     .             cp1           .
        +------------------------------------------------>
              .                           .
              .                           .
              .         duration          .
              <--------------------------->
              ti                          tf

 */

export class Animation {

  private tstart : number;
  private _curve : CubicBezier|null;

  static LINEAR = 1;
  static EASEIN = 2;
  static EASEOUT = 3;
  static EASEINOUT = 4;
  static EASECUBIC = 5;

  /**
   * @param duration Time in milliseconds to reach the target value
   * @param type A type value or cubic bezier curve for custom animation curve
   */
  constructor(
    private duration:number,
    private type:number,
    private onEval:(u:number)=>void,
    private onEnd?:()=>void
  )
  {
    this.tstart = NaN;
  }

  /**
   * Start the animation at given time instant
   * Will be invoked by the Animator when this animation is picked from the
   * queue of animations
   * @param t
   */
  start(t:number) {
    this.tstart = t;
    let ti = t;
    let tf = t + this.duration;
    switch(this.type) {
      case Animation.LINEAR:
        this._curve = null;
        break;
      case Animation.EASEIN:
        this._curve = new CubicBezier([
          [ti, 0.0],
          [ti+(tf-ti)/2, 0.0],
          [tf, 1.0],
          [tf, 1.0]
        ]);
        break;
      case Animation.EASEOUT:
        this._curve = new CubicBezier([
          [ti, 0.0],
          [ti, 0.0],
          [ti+(tf-ti)/2, 1.0],
          [tf, 1.0]
        ]);
        break;
      case Animation.EASEINOUT:
        this._curve = new CubicBezier([
          [ti, 0.0],
          [ti+(tf-ti)/2, 0.0],
          [ti+(tf-ti)/2, 1.0],
          [tf, 1.0]
        ]);
        break;
      default:
        throw new Error('Not implemented');
    }
  }

  end() {
    if(this.onEnd) {
      this.onEnd();
    }
  }

  _calcParameter(t:number) {
    let u = (t-this.tstart)/this.duration;
    switch(this.type) {
      case Animation.LINEAR:
        return u;
      case Animation.EASEIN:
        return this._curve!.evaluate(u)[1];
      case Animation.EASEOUT:
        return this._curve!.evaluate(u)[1];
      case Animation.EASEINOUT:
        return this._curve!.evaluate(u)[1];
      default:
        throw new Error('Not implemented');
    }
  }

  eval(t:number) {
    let u = Math.min(1.0, Math.max(0.0, this._calcParameter(t)));
    if(this.onEval) {
      this.onEval(u);
    }
  }

  isStarted() {
    return !isNaN(this.tstart);
  }

  isPastEnd(t:number) {
    return (t-this.tstart) > this.duration;
  }
}



