
import {Terra} from './terra'

export class TriTerra extends Terra {

  deriveGridSize() {
    let S = this.cellsize;
    let H = this.cellsize*Math.sin(Math.PI/3);
    this.nrows = Math.ceil(this.height/H)-1;
    this.ncols = 2*Math.ceil(this.width/S)-2;
  }

  deriveTerraDimensions() {
    let S = this.cellsize;
    let H = this.cellsize*Math.sin(Math.PI/3);
    this.width = (this.ncols+2)*S/2;
    this.height=(this.nrows+1)*H;
  }

  public getNeighbors(row:number,col:number) : number[][] {
    let neighbors;
    let nrows = this.nrows;
    let ncols = this.ncols;
    if((row+col)%2) { // 1,3,5,7,...
      neighbors = [
        [row,col-1],
        [row,col+1],
        [row+1,col]
      ]
    } else { // 0,2,4,6,...
      neighbors = [
        [row-1,col],
        [row,col-1],
        [row,col+1]
      ]
    }
    return neighbors.filter(([row,col]) => {
      return row >= 0 && row < nrows && col >= 0 && col < ncols;
    });
  }

  protected isValidCellCoord(coord:number[]) {
    coord;
    return true; // TODO
  }

  protected distanceManhattan(a:number[],b:number[]) {
    return Math.abs(b[0]-a[0])+Math.abs(b[1]-a[1]);
  }

  protected heuristic(from:number[], to:number[]) {
    return this.distanceManhattan(from,to);
  }

  public getCellCenter(row:number, col:number) {
    console.error('Not implemented');
    return [row,col];
  }

  protected getCellCorners(i:number,j:number) {
    let S = this.cellsize;
    let H = S*Math.sin(Math.PI/3);
    if((i+j)%2) { // 1,3,5,7,...
      // Flat-side down triangle
      return [
        [j*S/2+S/2,i*H],
        [(j-1)*S/2+S/2,(i+1)*H],
        [(j+1)*S/2+S/2,(i+1)*H],
      ];
    } else { // 0,2,4,6,...
      // Flat-side up triangle
      return [
        [(j-1)*S/2+S/2,i*H],
        [(j+1)*S/2+S/2,i*H],
        [j*S/2+S/2,(i+1)*H]
      ];
    }
  }

}


